import React, { useState, useRef } from "react";
import emailjs from "emailjs-com";
import "../contact.css";

export default function Contact() {
  const [name, setNames] = useState("");
  const [email, setMail] = useState("");
  const [message, setMessage] = useState("");
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();
    //  console.log(e);
    console.log(form.current);

    const templateparams = {
      from_name: name,
      from_email: email,
      // to_name: "mcelie.mundeke@gmail.com",
      feedback: message + " sent by: " + email
    };
    emailjs
      .send(
        "service_bfjgyl9",
        "template_zhkwgzm",
        templateparams,
        "gBh7M3M3mphHhtM_Z"
      )
      .then(
        function (response) {
          alert("Message Sent Successfully!", response.status, response.text);
          // e.target.reset();
        },
        function (error) {
          alert("Message Failed To Send...", error);
        }
      );
  };

  return (
    <>
      <div>
        <div className="contact-me-card row">
          <div className="col-lg-6 col-md-5 col-sm-12 left-contact px-2 py-2">
            <span className="get-in-touch mx-4 my-5"> Get in touch </span>

            <div className="py-5 d-flex justify-content-center">
              <lottie-player
                src="https://assets10.lottiefiles.com/packages/lf20_3ktmthuy.json"
                background="transparent"
                speed="1"
                style={{ width: "300px" }}
                loop
                autoplay
              ></lottie-player>
            </div>
          </div>

          <div className="col-lg-6 col-md-5 col-sm-12 my-auto">
            <form
              ref={form}
              onSubmit={sendEmail}
              className="d-flex flex-column"
            >
              <div className="input-group my-3 d-flex flex-column">
                <label> Full Name </label>
                <input
                  value={name}
                  name="user_name"
                  onChange={(e) => {
                    setNames(e.target.value);
                  }}
                  type="text"
                  placeholder="enter your name"
                  className="input-groups"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <label> Email </label>
                <input
                  value={email}
                  name="user_email"
                  onChange={(e) => {
                    setMail(e.target.value);
                  }}
                  type="text"
                  placeholder="enter your email"
                  className="input-groups"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <label> Message </label>
                <textarea
                  value={message}
                  name="message"
                  onChange={(e) => {
                    setMessage(e.target.value);
                  }}
                  type="text"
                  placeholder="Write your message..."
                  className="input-groups"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <input
                  className="btn btn-success col-lg-6 col-md-4 col-sm-2"
                  type="submit"
                  value="Send Message"
                />
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
