import React from "react";
import "../education.css";
import educations from "../integral/education-content";

const Education = () => (
  <>
    {educations.map((edu, key) => (
      <div className="d-flex flex-column">
        <div className="education-one my-4 justify-content-between d-flex flex-row">
          <div className="d-flex flex-column new-one">
            <span className="university-name">{edu.name}</span>
            <span className="university-degree">{edu.title}</span>
          </div>
          <div>
            <span className="year-passedout">{edu.date}</span>
          </div>
        </div>
      </div>
    ))}
  </>
);

export default Education;
